:root {
	--mainBackgroundColor: #3a4764;
	--keypadbackground: #232c43;
	--screen: #182034;
	--screenTextColor: #fff;
	--sliderBackgroundColor: #eae3dc;
	--keyType1: #eae3dc;
	--keyHoverType1: #dad3cd;
	--keyActiveType1: #b2ada9;
	--keyShadowType1: #b4a597;
	--keyType2: #637097;
	--keyHoverType2: #546082;
	--keyActiveType2: #4f5a76;
	--keyShadowType2: #404e72;
	--keyType3: #d03f2f;
	--keyHoverType3: #ac3427;
	--keyActiveType3: #9e3024;
	--keyShadowType3: #93261a;
	--textColor: #444b5a;
	--textColorSecond: #fff;
	--textColorEql: #fff;
	--textColorHead: #fff;
}
[data-theme="2"] {
	--mainBackgroundColor: #e6e6e6;
	--keypadbackground: #d1cccc;
	--screen: #ededed;
	--screenTextColor: #35352c;
	--sliderBackgroundColor: #d1cccc;
	--keyType1: #e5e4e1;
	--keyHoverType1: #d6d6d5;
	--keyActiveType1: #c5c5c4;
	--keyShadowType1: #a69d91;
	--keyType2: #377f86;
	--keyHoverType2: #306e74;
	--keyActiveType2: #285c61;
	--keyShadowType2: #1b5f65;
	--keyType3: #ca5502;
	--keyHoverType3: #b84f05;
	--keyActiveType3: #ab4904;
	--keyShadowType3: #893901;
	--textColor: #35352c;
	--textColorSecond: #fff;
	--textColorEql: #fff;
	--textColorHead: #35352c;
}

[data-theme="3"] {
	--mainBackgroundColor: #160628;
	--keypadbackground: #1d0934;
	--screen: #1d0934;
	--screenTextColor: #ffe53d;
	--sliderBackgroundColor: #1d0934;
	--keyType1: #341c4f;
	--keyHoverType1: #28163d;
	--keyActiveType1: #26153a;
	--keyShadowType1: #871c9c;
	--keyType2: #58077d;
	--keyHoverType2: #500772;
	--keyActiveType2: #40075b;
	--keyShadowType2: #bc15f4;
	--keyType3: #00e0d1;
	--keyHoverType3: #03a69b;
	--keyActiveType3: #07938a;
	--keyShadowType3: #6cf9f2;
	--textColor: #ffe53d;
	--textColorSecond: #fff;
	--textColorEql: #1b2428;
	--textColorHead: #ffe53d;
}
* {
	transition: all 0.2s;
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: "League Spartan";
	font-weight: 700;
}

body {
	height: var(--app-height);
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: var(--mainBackgroundColor);
}
#app {
	width: min(540px, 100%);
}
main {
	width: 100%;
}

.head {
	width: 100%;
	display: flex;
	justify-content: space-between;
}

.head h2 {
	font-size: 31px;
	color: var(--textColorHead);
}

.sliderBox {
	display: flex;
	flex-direction: row;
	align-items: flex-end;
	gap: 22px;
}

.sliderBox p {
	margin-bottom: 5px;
	font-size: 12px;
	letter-spacing: 0.07em;
	color: var(--textColorHead);
}

.slider {
	width: 71px;
	display: flex;
	flex-direction: column;
}

.slider ul {
	list-style: none;
	display: flex;
	justify-content: space-between;
	padding: 0 10px;
}

.slider ul li {
	margin-bottom: 5px;
	font-size: 12px;
	letter-spacing: 0.07em;
	color: var(--textColorHead);
}

.slider input {
	padding: 0 5px;
	-webkit-appearance: none;
	width: 100%;
	height: 26px;
	background: var(--sliderBackgroundColor);
	outline: none;
	opacity: 0.7;
	border-radius: 13px;
	-webkit-transition: 0.2s;
	transition: opacity 0.2s;
}

.slider input:hover {
	opacity: 1;
}

.slider input::-webkit-slider-thumb {
	-webkit-appearance: none;
	appearance: none;
	width: 18px;
	height: 18px;
	border-radius: 50%;
	background-color: var(--keyType3);
	cursor: pointer;
}

.slider input::-moz-range-thumb {
	width: 18px;
	height: 18px;
	border-radius: 50%;
	background-color: var(--keyType3);
	cursor: pointer;
}

.screen {
	margin-top: 36px;
	margin-bottom: 24px;
	padding: 9px 33px;
	padding-bottom: 0;
	display: flex;
	justify-content: flex-end;
	align-items: center;
	width: 100%;
	height: 128px;
	background-color: var(--screen);
	border-radius: 8px;
}

.screen h1 {
	font-size: 46px;
	color: var(--screenTextColor);
}
.keyPad {
	height: 480px;
	padding: 32px 30px;
	background-color: var(--keypadbackground);
	border-radius: 9px;
	display: grid;
	gap: 25px;
	grid-template-columns: 1fr 1fr 1fr 1fr;
}

.keyPad button:nth-last-child(2),
.keyPad button:last-child {
	grid-column: span 2;
}

.keyPad button {
	height: 60px;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 8px;
	border: none;
	background-color: var(--keyType1);
	box-shadow: 0px 4px 0px 0px var(--keyShadowType1);
	color: var(--textColor);
	font-size: 32px;
}
#reset,
#del {
	font-size: 20px;
}

.keyPad button:hover {
	background-color: var(--keyHoverType1);
}

.keyPad button:active {
	background-color: var(--keyActiveType1);
}

.buttonClick {
	background-color: var(--keyActiveType1) !important;
}

#del,
#reset {
	background-color: var(--keyType2);
	box-shadow: 0px 4px 0px 0px var(--keyShadowType2);
	color: var(--textColorSecond);
}

#del:hover,
#reset:hover {
	background-color: var(--keyHoverType2);
}

#del:active,
#reset:active {
	background-color: var(--keyActiveType2);
}

.buttonClickEditFunc {
	background-color: var(--keyActiveType2) !important;
}

#eql {
	background-color: var(--keyType3);
	box-shadow: 0px 4px 0px 0px var(--keyShadowType3);
	color: var(--textColorEql);
}

#eql:hover {
	background-color: var(--keyHoverType3);
}

#eql:active {
	background-color: var(--keyActiveType3);
}
.buttonClickEql {
	background-color: var(--keyActiveType3) !important;
}
@media screen and (max-width: 540px) {
	body,
	#app {
		min-width: 300px;
		height: var(--app-height);
	}
	#app {
		width: 90%;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	.keyPad {
		gap: 13px;
	}
	.keyPad button {
		height: 64px;
	}
}
